export default {
	data() {
		return {
			id: "",
			result:null
		}
	},
	onLoad(options) {
		const that = this;
		that.id = options.id;
		that.getDetail(options.id);
		uni.showLoading({
			title:"加载中..."
		})
	},
	methods: {
		getDetail(id) {
			const that = this;
			let param = {
				id: id
			}
			that.$api.noticeDetail(param).then(res => {
				if (res.success) {
					that.result=res.datas;
				} else {
					uni.showToast({
						title: res.message,
						icon: "none"
					})
				}
				uni.hideLoading();
			})
		},
		gotoAdd() {
			const that = this;
			that.$Router.push({
				path: "/pages/my/manageNavs/notice/add"
			})
		}
	}
}
